* {
  /* box-sizing: border-box;
  border-color: RED;
  border-style: solid;
  border-width: 1px; */
}
h1,
h2,
h3,
h4,
h5,
h6 {
  color: #444;
  font-family: "Montserrat", sans-serif;
  font-optical-sizing: auto;
  font-weight: 600;
  font-style: normal;
}
p,
a,
li {
  font-family: "Lato", sans-serif;
  color: #666; /* Lighter gray for paragraph text */
  font-size: 15px;
}

body {
  margin: 0;
  background-color: #f8f8f8;
}

.page {
  max-width: 75%;
  margin: 0 auto;
  padding: 20px;
  position: relative;
  min-height: 87.95vh;
}

/* <!-- About me section > */
.bio-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap-reverse;
  position: realtive;
}

.bio-text {
  margin-bottom: 20px;
  line-height: 1.5;
  width: calc(100% - 420px);
  /* Adjust width to accommodate portrait div */
  margin-right: 20px;
}

.portrait {
  width: 400px;
}

.portrait img {
  max-width: 100%;
  height: auto;
  display: block;
  border-radius: 10px;
}

.contact-container {
  display: flex;
  justify-content: space-around;
  margin-bottom: 20px;
  margin-top: 20px;
  align-items: center;
}

.contact-info {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
  padding: 10px;
  border-radius: 10px;
}

.contact-icon {
  font-size: 2rem;
  margin-right: 10px;
  display: flex;
  align-items: center;
}

.info-text {
  /* font-size: 16px; */
  color: #333;
  text-decoration: none;
}

.contact-info:hover {
  background-color: #f3f3f3;
  cursor: pointer;
}

@media screen and (max-width: 850px) {
  .page {
    padding: 0px;
  }

  .contact-container {
    flex-wrap: wrap;
  }

  .contact-info {
    width: 100%;
  }

  .bio-text {
    width: 100%;
    margin-right: 0px;
    /* Set width to full on smaller screens */
  }

  .portrait {
    margin-right: 0;
    /* Remove margin on smaller screens */
    margin-bottom: 20px;
    /* Add margin below the image */
    width: 100%;
    /* Set width to full on smaller screens */
  }
}

/* <!-- Work Experience > */
.work-experience {
  margin-top: 20px;
  margin-bottom: 20px;
  padding: 20px;
  background-color: #f9f9f9;
  /* background-color: #e6d4ff; */
  border-radius: 10px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
}

.section-title {
  font-size: 28px;
  text-align: center;
  color: #333;
  margin-bottom: 20px;
}

.experience-item {
  background-color: #fff;
  /* background-color: #f5edff; */
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  margin-bottom: 20px;
}

.job-title {
  font-size: 24px;
  color: #333;
}

.company {
  font-style: italic;
  color: #666;
}

.duration {
  margin-bottom: 10px;
  color: #666;
}

.responsibilities {
  list-style-type: none;
  padding-left: 0;
}

.responsibilities li {
  font-size: 16px;
  color: #666;
  margin-bottom: 5px;
}

.view-resume-link {
  display: inline-block;
  /* margin-top: 20px; */
  /* color: #432fbf; Dark purple text color */
  text-decoration: none;
  /* position: absolute; */
  bottom: 10px;
  right: 10px;
  transition: transform 0.3s ease;
  /* Add transition effect */
}

.arrow {
  transform: rotate(45deg);
}

/* <!-- Home page --> */

.background {
  position: absolute;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100%;
  z-index: -1;
}

.welcome-page {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  min-height: 100%;
}

.welcome-text {
  position: absolute;
  top: 15%;
  width: 75%;
  color: #f9f9f9;

  text-shadow: -0.25px -0.25px 0 #000, 0.25px -0.25px 0 #000,
    -0.25px 0.25px 0 #000, 0.25px 0.25px 0 #000;
}
.welcome-text p {
  color: #f9f9f9;
}
.welcome-text h1 {
  color: #f9f9f9;
}

.projects-button {
  position: absolute;
  bottom: 20%;
  right: 5%;

  background-color: #432fbf;
  padding: 1rem;
  border-radius: 10px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  transition: transform 250ms;
}

.projects-button a {
  color: #f9f9f9;
  text-decoration: none;
  padding: 1rem;
  /* text-shadow:
        -0.25px -0.25px 0 #000,
        0.25px -0.25px 0 #000,
        -0.25px 0.25px 0 #000,
        0.25px 0.25px 0 #000; */
}

.projects-button:hover {
  cursor: pointer;
  transform: translateY(-2px);
  color: #fff;
  background-color: #7263cf;
}

/* <!-- Projects page --> */

.filter-container {
  display: flex;
  justify-content: space-around;
  gap: 2rem;
  flex-wrap: wrap;
}

.projects-container {
  display: flex;
  flex-wrap: wrap;
  /* gap: 2rem; */
  align-content: center;
  align-items: center;
  justify-content: center;
}

.ending {
  /* position: fixed; */
  /* bottom: 10px; */
  /* left: 50%; */
  /* transform: translateX(-50%); */
  text-align: center;
  padding: 10px;
  /* text-align: center; */
  /* position: fixed; */
  /* bottom: 0; */
  width: 100%;
}

.ending p {
  margin: 0;
}

.ending a {
  color: #007bff;
  text-decoration: none;
}

.ending a:hover {
  text-decoration: underline;
}

/* <!-- Projects Template page --> */
.project-page {
  max-width: 75%;
  margin: 0 auto;
  padding: 20px;
  position: relative;
  min-height: 87.95vh;
}

/* Full-width for videos and headers */
.project-page video,
.project-page h1,
.project-page h2,
.project-page h3,
.project-page h4,
.project-page h5 {
  width: 100%;
}

/* Images and paragraphs */
.project-page img {
  width: 100%; /* Full width within their grid cell */
  max-width: 650px;
  height: auto;
  margin: 0 auto;
}
